 * .top- (TopPage)...カテゴリートップページ
 * .sub- (SubPage)...カテゴリー下層ページ
 * .products- (Products)...製品情報
 * .news- (News)...ニュース
 * .company- (Company)...会社案内・企業情報
 * .recruit- (Recruit)...採用情報ページ
 * .csr- (CorporateSocialResponsibility)...企業の社会的責任
 * .faq- (FrequentlyAskedQuestions)...よくある質問
 * .inquiry- (Inquiry)...お問い合わせ
 * .ir- (InvestorRelations)...投資家向け情報
 * .results- (Results)...検索結果ページ
 * .sitemap- (Sitemap)...サイトマップページ
 */
/*
---
name: sw-Button
namespace: SiteWide
category: [Button, Link, Form]
---

ボタンのデフォルトスタイルです。

```jade
a.sw-Button(href="#") aタグボタン
a.sw-Button.sw-Button-disabled(href="#") aタグボタン
button.sw-Button(type="button") butotnタグボタン
button.sw-Button(disabled type="button") butotnタグボタン
input.sw-Button(type="button" value="inputボタン")
input.sw-Button(type="button" value="inputボタン" disabled)
```
*/
.sw-Button {
  display: inline-block;
  margin: 0;
  padding: 0.75em 2em;
  border: none;
  border: 1px solid #d0d0d0;
  border-radius: 3px;
  color: inherit;
  font-family: inherit;
  font-size: 1em;
  line-height: 1;
  text-align: center;
  text-decoration: none;
  background: transparent;
  background-color: #fff;
  appearance: none;
  cursor: pointer;
}

.sw-Button[type=button], .sw-Button[type=reset], .sw-Button[type=submit] {
  appearance: none;
}

.sw-Button:hover, .sw-Button:active, .sw-Button:focus {
  text-decoration: none;
}

.sw-Button:focus {
  outline-width: 0;
  box-shadow: 0 0 5px #1589ee;
}

.sw-Button:focus:not(.focus-visible) {
  outline-width: 0;
  box-shadow: none;
}

.sw-Button:disabled, .sw-Button-disabled {
  opacity: 0.5;
  border-color: #ddd;
  background-color: #ddd;
  cursor: not-allowed;
}

.sw-Button-disabled {
  pointer-events: none;
}

/*
---
name: sw-Caption
namespace: SiteWide
category: Image
---

画像にキャプション（短い説明文）をつけます。キャプションは画像の横幅を基準に折り返されます。

ベーシックなスタイルです。

```jade
figure.sw-Caption
  img.sw-Caption_Image(src="https://placehold.jp/300x200.png" alt="")
  figcaption.sw-Caption_Text 画像にキャプション（短い説明文）をつけます。キャプションは画像の横幅を基準に折り返されます。
```

`.sw-Caption_Text-center`でキャプションをセンタリングします。

```jade
figure.sw-Caption
  img.sw-Caption_Image(src="https://placehold.jp/300x200.png" alt="")
  figcaption.sw-Caption_Text.sw-Caption_Text-center 画像にキャプション（短い説明文）をつけます。キャプションは画像の横幅を基準に折り返されます。
```

`.sw-Caption_Text-right`でキャプションを右揃えにします。

```jade
figure.sw-Caption
  img.sw-Caption_Image(src="https://placehold.jp/300x200.png" alt="")
  figcaption.sw-Caption_Text.sw-Caption_Text-right画像にキャプション（短い説明文）をつけます。キャプションは画像の横幅を基準に折り返されます。
```

`.sw-Caption-center`でコンポーネント自体をセンタリングします。

```jade
figure.sw-Caption.sw-Caption-center
  img.sw-Caption_Image(src="https://placehold.jp/300x200.png" alt="")
  figcaption.sw-Caption_Text 画像にキャプション（短い説明文）をつけます。キャプションは画像の横幅を基準に折り返されます。
```
*/
/**
 * 1. キャプションの最小幅です。
 *    画像が指定幅以下になってもキャプションの幅はそれ以上小さくなりません。
 */
.sw-Caption {
  display: table;
  min-width: 100px;
  /* 1 */
}

/**
 * 1. キャプションは常に画像の下に配置します
 */
.sw-Caption_Text {
  display: table-caption;
  margin-top: 0.5em;
  font-size: 0.85em;
  line-height: 1.4;
  caption-side: bottom;
  /* 1 */
}

/**
 * コンポーネント自体をセンタリングします。
 */
.sw-Caption-center {
  margin-right: auto;
  margin-left: auto;
}

/**
 * テキストをセンタリングします。
 */
.sw-Caption_Text-center {
  text-align: center;
}

/**
 * テキストを右揃えにします。
 */
.sw-Caption_Text-right {
  text-align: right;
}

/*
---
name: sw-Delimiter
namespace: SiteWide
category: Text
---

途中で改行をさせたくないフレーズやキーワードに指定します。囲まれた範囲（Delimiter）がまとまって改行されます。

```jade
p テキストは基本的にスペースや役物を基準に改行されますが、
  span.sw-Delimiter この部分はまとめて改行されます。
  | ここは通常のテキストです。
```
*/
.sw-Delimiter {
  display: inline-block;
}

/*
---
name: sw-Divider
namespace: SiteWide
category: Text
---

`<hr>`のような区切り記号を使って、分割・グルーピングします。

```jade
p.st-Text テキストです。
hr.sw-Divider
p.st-Text テキストです。
```
*/
.sw-Divider {
  box-sizing: inherit;
  display: block;
  width: 100%;
  height: auto;
  margin: 35px auto;
  border: 0;
  border-top: 1px solid #e5e5e5;
}

/*
---
name: sw-FormCheckbox
namespace: SiteWide
category: Form
---

`type="checkbox"`のデフォルトスタイルです。

```jade
input.sw-FormCheckbox(type="checkbox" id="checkbox1" checked)
label(for="checkbox1") チェックボックス
br
input.sw-FormCheckbox(type="checkbox" id="checkbox2")
label(for="checkbox2") チェックボックス
br
input.sw-FormCheckbox(disabled type="checkbox" id="checkbox3")
label(disabled for="checkbox3") チェックボックス
```
*/
.sw-FormCheckbox {
  /* デフォルトのチェックボックスを非表示にする。 */
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  overflow: hidden;
  padding: 0;
  clip: rect(0, 0, 0, 0);
}

.sw-FormCheckbox + label {
  position: relative;
  display: inline-block;
  padding-left: 1.25em;
  cursor: pointer;
}

.sw-FormCheckbox + label:before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 1em;
  height: 1em;
  margin: auto;
  border: 1px solid #767676;
  border-radius: 3px;
  vertical-align: text-bottom;
  background-repeat: no-repeat;
  background-size: contain;
}

.sw-FormCheckbox:focus + label:before {
  border-color: #1589ee;
  box-shadow: 0 0 5px #1589ee;
}

.sw-FormCheckbox:focus:not(.focus-visible) + label:before {
  border-color: #767676;
  box-shadow: none;
}

.sw-FormCheckbox:checked + label:before {
  border: 0;
  background-image: url("/assets/img/SiteWide/Icon/FormCheckbox_on.svg");
}

.sw-FormCheckbox:disabled,
.sw-FormCheckbox + label[disabled] {
  cursor: not-allowed;
  opacity: 0.5;
}

/*
---
name: sw-FormInput
namespace: SiteWide
category: Form
---

`<input>`タグのデフォルトスタイルです。

```jade
input.sw-FormInput(type="input" placeholder="デフォルト")

label.sw-FormInput_TextTop(for="input1") inputタグの説明です
input.sw-FormInput(type="input" name="email" autocomplete="email" id="input1" placeholder="example@gmail.com")

input.sw-FormInput(type="input" name="name" autocomplate="name" id="input2" placeholder="山田 太郎")
label.sw-FormInput_TextTop(for="input2") inputタグの説明です

input.sw-FormInput(disabled type="input" placeholder="disabled")
```
*/
.sw-FormInput {
  box-sizing: border-box;
  width: 100%;
  height: 42px;
  padding: 0 1em;
  border: 1px solid #d0d0d0;
  border-radius: 3px;
  font-size: 16px;
  vertical-align: middle;
  background-color: #fff;
  appearance: none;
  box-shadow: 1px 1px 6px -3px #9e9e9e inset;
  /**
   * Normalize.cssのリセットをします。
   */
}

.sw-FormInput[type="search"] {
  box-sizing: border-box;
  appearance: none;
}

.sw-FormInput::placeholder {
  color: #767676;
  font-size: 0.85em;
}

.sw-FormInput:focus {
  outline-width: 0;
  border-color: #1589ee;
  box-shadow: 0 0 5px #1589ee;
}

.sw-FormInput:disabled {
  opacity: 0.5;
  background-color: #ddd;
  cursor: not-allowed;
}

.sw-FormInput_TextTop {
  display: inline-block;
  margin-bottom: 0.25em;
}

.sw-FormInput_TextBottom {
  display: inline-block;
  margin-top: 0.25em;
}

/*
---
name: sw-FormRadio
namespace: SiteWide
category: Form
---

`type="radio"`のデフォルトスタイルです。

```jade
input.sw-FormRadio(type="radio" id="radio1" name="radio1" checked)
label(for="radio1") ラジオボタン
br
input.sw-FormRadio(type="radio" id="radio2" name="radio1")
label(for="radio2") ラジオボタン
br
input.sw-FormRadio(disabled type="radio" id="radio3" name="radio3")
label(disabled for="radio3") ラジオボタン
```
*/
.sw-FormRadio {
  /* デフォルトのラジオボタンを非表示にする。 */
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  overflow: hidden;
  padding: 0;
  clip: rect(0, 0, 0, 0);
}

.sw-FormRadio + label {
  display: inline-block;
  position: relative;
  padding-left: 1.25em;
  cursor: pointer;
