  li 入れ子に対応した順序リストです。入れ子に対応した順序リストです。
  li 入れ子に対応した順序リストです。
    ol
      li 入れ子に対応した順序リストです。入れ子に対応した順序リストです。
      li 入れ子に対応した順序リストです。
```
*/
.sw-ListOrder {
  display: table;
  padding-left: 0;
  list-style-type: none;
  counter-reset: order;
}

.sw-ListOrder > li {
  display: table-row;
  counter-increment: order;
}

.sw-ListOrder > li:before {
  content: counters(order, "-") ".";
  display: table-cell;
  padding-right: 0.5em;
  text-align: right;
  white-space: nowrap;
}

.sw-ListOrder ol {
  padding-left: 0;
  list-style-type: none;
  counter-reset: order;
}

.sw-ListOrder-from2 {
  counter-reset: order 1;
}

.sw-ListOrder-from3 {
  counter-reset: order 2;
}

.sw-ListOrder-from4 {
  counter-reset: order 3;
}

.sw-ListOrder-from5 {
  counter-reset: order 4;
}

.sw-ListOrder-from6 {
  counter-reset: order 5;
}

.sw-ListOrder-from7 {
  counter-reset: order 6;
}

.sw-ListOrder-from8 {
  counter-reset: order 7;
}

.sw-ListOrder-from9 {
  counter-reset: order 8;
}

.sw-ListOrder-from10 {
  counter-reset: order 9;
}

