  p.st-Text.sw-Text どこで生れたかとんと見当がつかぬ。
```
*/
.st-Text + .st-Text,
:not(.st-Text) + .st-Text {
  margin-top: 35px;
}

@media print, screen and (min-width: 768px) {
  .st-Text + .st-Text,
  :not(.st-Text) + .st-Text {
    margin-top: 50px;
  }
}

.st-Heading2 + .st-Text,
.st-Heading3 + .st-Text,
.st-Heading4 + .st-Text,
.st-Heading5 + .st-Text,
.st-Lead + .st-Text {
  margin-top: 0;
}

