---
name: st-Title
namespace: Structure
category: Layout
---

ページタイトルの余白を指定します。

```html
.st-Wrapper
  h1.sw-Title.st-Title ページタイトル
```
*/
.st-Title {
  margin-bottom: 25px;
}

@media print, screen and (min-width: 768px) {
  .st-Title {
    margin-bottom: 40px;
  }
}

/*
---
name: st-Wrapper
namespace: Structure
category: Layout
---

```jade
.st-Wrapper
  section.st-Section2
    h2.st-Heading2.sw-Heading2 吾輩はここで始めて人間というものを見た。
    p.st-Text.sw-Text しかもあとで聞くとそれは書生という人間中で一番獰悪な種族であったそうだ。この書生というのは時々我々を捕えて煮て食うという話である。
```

```jade
.st-Wrapper.st-Wrapper_Full
  section.st-Section2
    h2.st-Heading2.sw-Heading2 吾輩はここで始めて人間というものを見た。
    p.st-Text.sw-Text しかもあとで聞くとそれは書生という人間中で一番獰悪な種族であったそうだ。この書生というのは時々我々を捕えて煮て食うという話である。
```

```jade
.st-Wrapper.st-Wrapper_FullWrapst-Wrapper_FullWrap
  section.st-Section2
    h2.st-Heading2.sw-Heading2 吾輩はここで始めて人間というものを見た。
    p.st-Text.sw-Text しかもあとで聞くとそれは書生という人間中で一番獰悪な種族であったそうだ。この書生というのは時々我々を捕えて煮て食うという話である。
```
*/
.st-Wrapper {
  max-width: 1170px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 20px;
}

@media print, screen and (min-width: 1170px) {
  .st-Wrapper {
    padding: 0;
  }
}

.st-Wrapper_Full {
  margin-left: calc(-50vw + 50%) !important;
  margin-right: calc(-50vw + 50%) !important;
}

.st-Wrapper_FullWrap {
  margin-left: calc(-50vw + 50%) !important;
  margin-right: calc(-50vw + 50%) !important;
  padding-right: calc(50vw - 50%) !important;
  padding-left: calc(50vw - 50%) !important;
}

/*
---
name: test-Block
namespace: test
category: Block
---

スタイルガイド用のダミーブロックです。

```jade
.st-Wrapper
  .test-Block
    .test-Block_Left
      img(src="https://placehold.jp/300x200.png" alt)
    .test-Block_Right
      h3 ダミーの見出しです
      p ダミーのテキストです。ダミーのテキストです。ダミーのテキストです。ダミーのテキストです。ダミーのテキストです。
```
*/
.test-Block:after {
  content: "";
  display: block;
  clear: both;
}

.test-Block_Left {
  margin-bottom: 1em;
}

@media print, screen and (min-width: 1024px) {
  .test-Block_Left {
    float: left;
    margin-right: 1em;
    margin-bottom: 0;
  }
}

.test-Block_Right {
  overflow: hidden;
}

.test-Block_Right h3 {
  margin-bottom: 1em;
}

.test-Block_Right p + p {
  margin-top: 1em;
}

.test-Box {
  padding: 2rem;
  text-align: center;
  font-size: 1rem;
  letter-spacing: 0.1em;
  color: #fff;
  background-color: #aaa;
}

